.App {
  text-align: center;
}

main {
  padding: 30px 0px;
}

/* Grid System */

.site-grid {
  display: grid
}

.site-grid-system {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
}

.site-span-1 {
  grid-column: span 1;
}
.site-span-2 {
  grid-column: span 2;
}

.site-span-3 {
  grid-column: span 3;
}

.site-span-4 {
  grid-column: span 4;
}

.site-span-5 {
  grid-column: span 5;
}

.site-span-6 {
  grid-column: span 6;
}

.site-span-7 {
  grid-column: span 7;
}

.site-span-8 {
  grid-column: span 8;
}

.site-span-9 {
  grid-column: span 9;
}

.site-span-10 {
  grid-column: span 10;
}

.site-span-11 {
  grid-column: span 11;
}

.site-span-12 {
  grid-column: span 12;
}

.site-border {
  border: 1px solid black;
}

.site-border-white {
  border: 1px solid white;
}

.site-shadow {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 10px 6px 20px 0 rgba(0, 0, 0, 0.19)
}

.site-responsive-image {
  width: 100%;
  height: auto;
  max-height: 100%;
  border-radius: 3px;
}

.site-external-link {
  text-decoration: none;
}

/* Width and Height */
.w-100 {
  width: 100%;
}

.w-90 {
  width: 90%;
}

.w-80 {
  width: 80%;
}

.w-70 {
  width: 70%;
}

.w-60 {
  width: 60%;
}

.w-50 {
  width: 50%;
}

.w-40 {
  width: 40%;
}

.w-30 {
  width: 30%;
}

.w-20 {
  width: 20%;
}

.w-10 {
  width: 10%;
}

.h-100 {
  height: 100%;
}

.h-90 {
  height: 90%;
}

.h-80 {
  height: 80%;
}

.h-70 {
  height: 70%;
}

.h-60 {
  height: 60%;
}

.h-50 {
  height: 50%;
}

.h-40 {
  height: 40%;
}

.h-30 {
  height: 30%;
}

.h-20 {
  height: 20%;
}

.h-10 {
  height: 10%;
}

/* Auto */

.h-auto {
  height: auto;
}

.w-auto {
  width: auto;
}

.m-auto {
  margin: auto;
}

.text-center {
  text-align: center;
}

/* overflow */
.site-overflow {
  overflow: scroll;
}

/** Cursor **/
.cursor-pointer {
  cursor: pointer;
}

